export const colors = {
  namespaced: true,
  state: {
    colorMode: [
      {
        id: 1,
        name: "light_mode",
        img: require("@/assets/color/light_mode.svg"),
        color: {
          primary: "#f6f4ee",
          secondary: "#171717",
          success: "#175f36",
          alert: "#941010",
        },
      },
      {
        id: 2,
        name: "dark_mode",
        img: require("@/assets/color/dark_mode.svg"),
        color: {
          primary: "#171717",
          secondary: "#f6f4ee",
          success: "#229755",
          alert: "#d41f1f",
        },
      },
    ],
    colorSpecs: [
      {
        id: 1,
        name: "light_protan",
        img: require("@/assets/color/light_protan.svg"),
        color: {
          primary: "#f6f4ee",
          secondary: "#171717",
          success: "#4550d4",
          alert: "#c5b02b",
        },
      },
      {
        id: 2,
        name: "dark_protan",
        img: require("@/assets/color/dark_protan.svg"),
        color: {
          primary: "#171717",
          secondary: "#f6f4ee",
          success: "#4550d4",
          alert: "#fdd200",
        },
      },
      {
        id: 3,
        name: "light_deutan",
        img: require("@/assets/color/light_deutan.svg"),
        color: {
          primary: "#f6f4ee",
          secondary: "#171717",
          success: "#1a68b7",
          alert: "#ffb51b",
        },
      },
      {
        id: 4,
        name: "dark_deutan",
        img: require("@/assets/color/dark_deutan.svg"),
        color: {
          primary: "#171717",
          secondary: "#f6f4ee",
          success: "#1a68b7",
          alert: "#ffb51b",
        },
      },
      {
        id: 5,
        name: "light_tritan",
        img: require("@/assets/color/light_tritan.svg"),
        color: {
          primary: "#f6f4ee",
          secondary: "#171717",
          success: "#00bbb6",
          alert: "#d60202",
        },
      },
      {
        id: 6,
        name: "dark_tritan",
        img: require("@/assets/color/dark_tritan.svg"),
        color: {
          primary: "#171717",
          secondary: "#f6f4ee",
          success: "#00bbb6",
          alert: "#d60202",
        },
      },
    ],
    colorContrast: [
      {
        id: 1,
        name: "light_high_contrast",
        img: require("@/assets/color/light_high_contrast.svg"),
        color: {
          primary: "#f6f4ee",
          secondary: "#171717",
          success: "#175f36",
          alert: "#941010",
        },
      },
      {
        id: 1,
        name: "dark_high_contrast",
        img: require("@/assets/color/dark_high_contrast.svg"),
        color: {
          primary: "#171717",
          secondary: "#f6f4ee",
          success: "#229755",
          alert: "#d41f1f",
        },
      },
    ],
  },
  mutations: {
    setColor(state, colorName) {
      let allColors = [
        ...state.colorMode,
        ...state.colorSpecs,
        ...state.colorContrast,
      ];
      let colors = allColors.find((color) => color.name === colorName);
      let root = document.querySelector(":root");

      root.style.setProperty("--primary-color", colors.color.primary);
      root.style.setProperty("--secondary-color", colors.color.secondary);
      root.style.setProperty("--success-color", colors.color.success);
      root.style.setProperty("--alert-color", colors.color.alert);
    },
  },
  actions: {},
};
